.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#camera-input {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

#camera-input + label {
  cursor: pointer;
  font-size: 1.25em;
  font-weight: 700;
  color: white;
  background-color: black;
  display: inline-block;
  padding: 5px 15px;
}

.button {
  padding: 5px 15px;
  font-size: 1.25em;
  font-weight: 700;
  color: white;
  background-color: #006793;
  margin-left: 10px;
  margin-right: 10px;
  border: none;
}

#camera-input:focus + label,
#camera-input + label:hover {
  background-color: red;
}

.pin-input-field {
  padding: 8px 10px;
  font-size: calc(10px + 1vmin);
  margin-top: 20px;
  margin-bottom: 20px;
}

.pin-input-submit {
  padding: 8px 14px;
  background: #006793;
  font-size: 16px;
  border: none;
  color: #fff;
}

.img-preview {
  width: 150px;
  height: 150px;
  object-fit: contain;
  border: #fff 1px solid;
}

.image-container {
  display: flex;
  flex-wrap: wrap;
  margin-top: 20px;
}
